.flip__box {
  flex-direction: column;
  align-items: center;
  gap: 30px;
  margin-bottom: 30px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .flip__box {
    flex-flow: wrap;
    justify-content: center;
    gap: 30px;
  }
}

@media screen and (min-width: 1280px) {
  .flip__box {
    flex-flow: row;
    display: flex;
  }
}

.flip-card {
  width: calc(100% - 60px);
  height: 200px;
  perspective: 1000px;
  background-color: rgba(0, 0, 0, 0);
}

@media screen and (min-width: 768px) {
  .flip-card {
    width: calc(50% - 30px);
  }
}

@media screen and (min-width: 1280px) {
  .flip-card {
    width: calc(25% - 15px);
  }
}

.flip-card-inner {
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  border-radius: 10px;
  transition: transform .8s;
  position: relative;
  box-shadow: 0 3px 8px rgba(0, 0, 0, .24);
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front, .flip-card-back {
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  position: absolute;
}

.flip-card-front {
  background-color: var(--card-bg-color);
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  padding: 30px;
  display: flex;
}

.flip-card-back {
  background-color: var(--card-bg-color);
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  padding: 30px;
  display: flex;
  transform: rotateY(180deg);
}

